___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  border: 0 solid transparent;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

#content{
  height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#wide-container{
  width: 100vw;
  display: flex;
  background-color: deepskyblue;
}

#picture-frame{
  display: flex;
  overflow: hidden;
  width: 400px;
  height: 300px;
 box-shadow: 2px 2px 5px darkgrey;
  border-radius: 10px;
  position:relative;
  margin-bottom: 10px;
  z-index: 0;
}

#button-div{  
  width: 400px;
  display: flex;
  justify-content: space-around;
}

.btn{
  height: 35px;
  width: 90px;
  margin: 5px;
  font-weight: bold;
  border-radius: 5px;
  box-shadow: 2px 2px 5px darkgrey;
}

.btn:hover{
  background-color: cadetblue;
}
.btn:active, .nav-dot:active{
  transform:translateX(2px) translateY(2px);
}

#nav-dot-container{
 position: absolute;
  bottom: 20px;
  width: 400px; 
  z-index: 1;
  display: flex;
  justify-content: center;
}

.nav-dot{
  width: 20px;
  height: 20px;
  margin: 0 20px;
  background-color: whitesmoke;
  opacity: 0.7;
  border-radius: 10px;
}

.nav-dot:hover, .active{
  opacity: 1;
  background-color: white;
}

.active{
box-shadow: 1px 1px 2px gray;
}




